import { FC } from 'react';
import styled from '@emotion/styled';

import { boxShadow } from '@/component/styles';

interface Props{
    header: string;
    children: React.ReactNode;
}

const Section = styled.section `
${({ theme }) =>
boxShadow(theme.component.shadow1, theme.component.shadow2)}
`;


export const Tile: FC<Props> = ({ header, children }) => (
<Section>
    <h2>{header}</h2>
    {children}
    </Section>
);


